import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, Router } from '@angular/router';
import { Observable, of, EMPTY } from 'rxjs';
import { mergeMap } from 'rxjs/operators';

import { ICaterers } from '../caterers.model';
import { CaterersService } from '../service/caterers.service';

@Injectable({ providedIn: 'root' })
export class CaterersRoutingResolveService implements Resolve<ICaterers | null> {
  constructor(protected service: CaterersService, protected router: Router) {}

  resolve(route: ActivatedRouteSnapshot): Observable<ICaterers | null | never> {
    const id = route.params['id'];
    if (id) {
      return this.service.find(id).pipe(
        mergeMap((caterers: HttpResponse<ICaterers>) => {
          if (caterers.body) {
            return of(caterers.body);
          } else {
            this.router.navigate(['404']);
            return EMPTY;
          }
        })
      );
    }
    return of(null);
  }
}
